                type="date"
                placeholder="Pick a future date">
            </el-date-picker>
        </el-form-item>
        <el-form-item label="Question 1">
            <div style="display:flex; ">
                <el-select v-model="question1.type" placeholder="Select">
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
            <el-input v-model="question1.text"></el-input></div>
        </el-form-item>
        <el-form-item label="Question 2">
            <div style="display:flex; ">
                <el-select v-model="question2.type" placeholder="Select">
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
            <el-input v-model="question2.text"></el-input></div>
        </el-form-item>
        <el-form-item label="Question 3">
            <div style="display:flex; ">
                <el-select v-model="question3.type" placeholder="Select">
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
            <el-input v-model="question3.text"></el-input></div>
        </el-form-item>
        <el-form-item label="Question 4">
            <div style="display:flex; ">
                <el-select v-model="question4.type" placeholder="Select">
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
            <el-input v-model="question4.text"></el-input></div>
        </el-form-item>
