<script>
import db from '../firebase/init.js';
import { doc, getDoc, setDoc } from "firebase/firestore"; 

export default {
  name: 'Admin',
  data() {
		return {
            options: [{
                value: 'yes/no',
                label: 'yes/no'
                }, {
                value: 'bar',
                label: 'slider'
                }, {
                value: 'music',
                label: 'music'
                }, {
                value: 'any',
                label: 'any'
                },{
                value: 'name',
                label: 'name'
                }],
            entryID: "",
            entries: [],
            date: null,
            question1: { text: "", type: "", answer: ""},
            question2: { text: "", type: "", answer: ""},
            question3: { text: "", type: "", answer: ""},
            question4: { text: "", type: "", answer: ""},
		}
	},
  methods: {
    validate() {
        var inputDate = this.date;
        let today = new Date();
        // if (inputDate.getTime() < today.getTime()) {
        //     this.date = null
        //     this.$notify.error({
        //         title: 'Error',
        //         message: 'Please choose a future date'
        //         });
        // }
    },
    async onSubmit() {
        let questions = [this.question1, this.question2, this.question3, this.question4]
        if (questions.some(question => !question.type)) {
            this.$notify.error({
                title: 'Error',
                message: 'Please specify all question types!'
                });
            return
        }
        if (questions.some(question => !question.text)) {
            this.$notify.error({
                title: 'Error',
                message: 'Please write all questions!'
                });
            return
        }
        var inputDate = this.date;
        var dd = String(inputDate.getDate()).padStart(2, '0');
        var mm = String(inputDate.getMonth() + 1).padStart(2, '0'); //January is 0!
        var yyyy = inputDate.getFullYear();

        inputDate = mm + '-' + dd + '-' + yyyy;
        let document = await getDoc(doc(db, "questions", inputDate))
        let exists = document.exists()
        await setDoc(doc(db, "questions", inputDate), {
            1: JSON.stringify(this.question1),
            2: JSON.stringify(this.question2),
            3: JSON.stringify(this.question3),
            4: JSON.stringify(this.question4),
        });
        if (exists) {
            this.$confirm('Success! Updated question for ' + inputDate)
                .then(_ => {
                    done();
                })
                .catch(_ => {});
        } else {
            this.$confirm('Success! Submitted question for ' + inputDate)
                .then(_ => {
                    done();
                })
                .catch(_ => {});
        }
        
    }
  }

}
</script>
