<template>
  <div class="entries">
    <el-card class="box-card">
        <el-form>
            <h3>Get your past entries</h3>
            <el-form-item>
                <el-input v-model="entryID" placeholder="Enter your most recent entry code here"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="getAllEntries">Submit</el-button>
            </el-form-item>
        </el-form>
    </el-card>
    <el-card class="box-card" v-for='(entry, i) in entries' :key="i">
        <div slot="header" class="clearfix">
            <span>{{entry.id}} {{ entry.date }}</span>
            <el-button style="float: right; padding: 3px 0" type="text">Operation button</el-button>
        </div>
        <div v-for="(question, j) in entry.content" :key="j" class="text item">
            {{ question.text }} {{ question.answer }}
        </div>
    </el-card>
  </div>
</template>
<script>
import db from '../firebase/init.js';
import { doc, getDoc } from "firebase/firestore"; 

export default {
  name: 'Entries',
  data() {
		return {
            entryID: "",
            entries: []
		}
	},
  methods: {
    async getAllEntries() {
        this.entries = []
        let visitedID = new Set()
        let document = await getDoc(doc(db, "entries", this.entryID.trim()))
        if (!document.exists()) {
            return
        }
        if (document.data().nextID) {
            return
        }
        while(true) {
            visitedID.add(document.id)
            this.entries.push({id: document.id, date: document.data().dayID, content: JSON.parse(document.data().answerArr)})
            if (!document.data().prevID) {
                break
            }
            let nextDoc = await getDoc(doc(db, 'entries', document.data().prevID))
            document = nextDoc
            if (document.id in visitedID) {
                return
            }
        }
    }
  }

}
</script>
<style>
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
    padding: 10px;
    margin: 20px;
  }
</style>

